.pan-btn {
  font-size: 14px;
  color: #fff;
  padding: 14px 36px;
  border-radius: 8px;
  border: none;
  outline: none;
  margin-right: 25px;
  -webkit-transition: 600ms ease all;
  transition: 600ms ease all;
  position: relative;
  display: inline-block;
}
.pan-btn:hover {
    background: #fff;
}
.pan-btn:hover:before, .pan-btn:hover:after {
      width: 100%;
      -webkit-transition: 600ms ease all;
      transition: 600ms ease all;
}
.pan-btn:before, .pan-btn:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 2px;
    width: 0;
    -webkit-transition: 400ms ease all;
    transition: 400ms ease all;
}
.pan-btn::after {
    right: inherit;
    top: inherit;
    left: 0;
    bottom: 0;
}
