
.editticket {
  margin: 50px;
  width: 80%;
}
.editticket .title {
    color: #feff25;
    font-size: 30px;
    padding-left: 10px;
}
.editticket .appendInfo .ticketinfo {
    margin-left: 5px;
}
.editticket .appendInfo .ticketinfo .han {
      color: rgba(43, 200, 13, 0.6);
      font-size: 16px;
}
.content {
  margin: 20px 5px;
}
.heng {
  margin: 20px 0;
  height: 1px;
  border: 0px;
  background-color: rgba(174, 127, 255, 0.38);
  color: #29e11c;
}
.ticketcomment .dialog-box {
  position: relative;
  left: 100px;
  margin-left: -20px;
}
.ticketcomment .dialog-box span {
    width: 0;
    height: 0;
    font-size: 0;
    overflow: hidden;
    position: absolute;
}
.ticketcomment .dialog-box span.bot {
      border-width: 16px;
      border-style: solid dashed dashed;
      border-color: transparent #4cff1e transparent transparent;
      top: 10px;
      left: -30px;
}
.ticketcomment .dialog-box span.top {
      border-width: 18px;
      border-style: solid dashed dashed;
      border-color: transparent #fff transparent transparent;
      top: 10px;
      left: -30px;
}
.ticketcomment .dialog-box .comment {
    border: solid 1px rgba(255, 164, 186, 0.62);
    padding: 6px;
    margin-bottom: 10px;
    min-height: 50px;
    max-width: 600px;
    width: 50rem;
}
.ticketcomment .dialog-box .comment .commenttime {
      float: right;
}



